import uniIcon from "@/components/uni-icons/uni-icons.vue";
export default {
	components:{
		uniIcon
	},
	props:{
		items:{
			type: Array
		},
		
		firstLabel:{
			type: String
		}
	},
	data() {
		return {
			index: -1
		};
	},
	
	methods:{
		itemChange:function(e){
			let index = e.detail.value
			this.index = index
			this.$emit("mychange",this.items[index].id)
		}
	}
}
